#landing {
  height: 100vh;
  background-image: url(/img/wreck.jpg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center center;
  background-size: cover;
}
#citat {
  display: block;
  margin: 2em 1em;
  padding: 0.3em 3em;
  cursor: default;
  text-align: center;
  color: $foreground-color;
  font-family: "Modeka";
  font-size: 3em;
  @media(max-width: 767px) {
    margin: 0.5em;
    padding: 0;
    font-size: 2.5em;
  }
  @media(min-width: 1367px) {
    margin-top: 4em;
    margin-bottom: 4em;
    font-size: 3.1em;
  }
}
a {
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  text-decoration: none;
}
.social {
  margin-top: 2em;
  margin-bottom: 2em;
  cursor: default;
  font-size: 1.5em;
  @media(max-width: 767px) {
    margin-top: 1em;
    margin-bottom: 1em;
    font-size: 1.5em;
  }
  a {
    margin: 0 0.07em;
    color: $primary-color;
    &:active {
      color: $foreground-color;
    }
    &:visited {
      color: $primary-color;
    }
    &:hover {
      color: $foreground-color;
    }
  }
}
.fa-paper-plane {
  font-size: 0.9em;
}
//.fa-codepen { font-size: 0.9em; position: relative; bottom: 0.09em; }
.shpan {
  color: $foreground-color;
}
footer {
  margin-bottom: 2em;
  cursor: default;
  a {
    color: $foreground-color;
    &:visited {
      color: $foreground-color;
    }
    &:hover {
      text-decoration: underline;
      color: $foreground-color;
    }
  }
}
